@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
*{margin:0px;padding:0px;font-family:'Roboto', sans-serif}.container{width:100vw;height:100vh;display:flex;justify-content:center;align-items:center}.card{width:948px;margin-left:10px;margin-right:10px;display:flex;box-shadow:5px 5px 15px rgba(43,42,42,0.5)}.col{margin:50px;display:flex;flex-direction:column;justify-content:center}.col.image>img{object-fit:cover;object-position:-100px 0px}h2{margin-bottom:20px}h4{margin-bottom:15px}p{color:#c7c7c7;font-size:13px;line-height:20px}.stast{margin-top:15px}.btn{cursor:pointer;color:white;padding-top:5px;padding-bottom:5px;padding-left:20px;padding-right:20px;border:none;margin-right:5px}.btn.btn-info{background-color:#00bcda}.btn.btn-primary{background-color:#3368e8}.btn.btn-info:hover,.btn.btn-primary:hover{opacity:0.8;box-shadow:5px 5px 15px rgba(43,42,42,0.5)}.slide-bottom{-webkit-animation:slide-bottom 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;animation:slide-bottom 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both}@-webkit-keyframes slide-bottom{0%{-webkit-transform:translateY(-100px);transform:translateY(-100px)}100%{-webkit-transform:translateY(0px);transform:translateY(0px)}}@keyframes slide-bottom{0%{-webkit-transform:translateY(-100px);transform:translateY(-100px)}100%{-webkit-transform:translateY(0px);transform:translateY(0px)}}@media screen and (min-width: 100px) and (max-width: 760px){.container{width:100%;height:100%;padding:0px}.card{width:100%;flex-direction:column-reverse;margin:40px}.col{margin:20px;justify-content:center;align-items:center}p{text-align:center}img{width:280px;height:280px}}

